@charset "UTF-8";
@tailwind base;
h1,
h2,
h3,
h4,
h5,
h6 {
  @apply font-sans font-normal text-black mb-2 leading-tight;
}

h1 {
  @apply text-5xl;
}

p,
input,
div {
  @apply font-sans font-light leading-normal text-grey-darker;
}

p {
  @apply mb-1;
}

a {
  @apply font-sans text-primary no-underline border-b border-transparent;
  transition: 0.2s ease;
}
a:hover {
  @apply border-primary;
}

.container {
  @apply px-1;
}

.title-display {
  @apply font-serif tracking-widest uppercase text-base relative;
}
.title-display.title-display-left {
  padding-left: 49px;
}
.title-display.title-display-left::before {
  content: "";
  width: 20px;
  height: 2px;
  position: absolute;
  top: 7px;
  left: 15px;
}
.title-display.title-display-left::after {
  content: "";
  width: 35px;
  height: 2px;
  position: absolute;
  top: 11px;
  left: 0;
}
.title-display.title-display-center::before {
  content: "";
  width: 2px;
  height: 35px;
  position: absolute;
  top: -37px;
  left: calc(50% - 2px);
  transform: translateX(-50%);
}
.title-display.title-display-center::after {
  content: "";
  width: 2px;
  height: 20px;
  position: absolute;
  top: -33px;
  left: calc(50% + 2px);
  transform: translateX(-50%);
}
.title-display.text-primary::before, .title-display.text-primary::after {
  background-color: #0076ff;
}

@screen md {
  .section_page-banner .content {
    max-width: 660px;
  }
}
@screen lg {
  .section_page-banner .content {
    max-width: 448px;
  }
}

#cookie-notice.active {
  @apply visible opacity-100 pointer-events-auto z-10 !important;
}
@screen md {
  #cookie-notice {
    width: 320px !important;
    transform: translateY(100%);
  }
  #cookie-notice.active {
    transform: translateY(0);
  }
}

#panel-share.active {
  @apply visible opacity-100;
}
@media (max-width: 767px) {
  #panel-share {
    @apply w-full h-full inset-0;
    background-color: rgba(0, 0, 0, 0.5);
  }
  #panel-share .panel-share {
    @apply absolute w-full bottom-0 left-0 transition;
    transform: translateY(100%);
  }
  #panel-share.active .panel-share {
    transform: translateY(0);
  }
}
@screen md {
  #panel-share {
    transform: translate(-50%, -80%);
    left: 50%;
    top: -0.75rem;
  }
  #panel-share.active {
    transform: translate(-50%, -100%);
  }
}

.arrow-bottom::after {
  @apply absolute rounded-sm;
  content: " ";
  transform: translateX(-50%);
  left: 50%;
  bottom: -10px;
  border-top: 10px solid white;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  border-bottom: none;
}

.mixitup-control {
  @apply text-grey-darker transition;
}
.mixitup-control.mixitup-control-active {
  @apply text-primary;
}

.justify-stretch {
  justify-content: stretch;
}

.inset-0 {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.inset-auto {
  top: auto;
  right: auto;
  bottom: auto;
  left: auto;
}

.inset-y-0 {
  top: 0;
  bottom: 0;
}

.inset-x-0 {
  right: 0;
  left: 0;
}

.inset-y-auto {
  top: auto;
  bottom: auto;
}

.inset-x-auto {
  right: auto;
  left: auto;
}

@responsive {
  .top-0 {
    top: 0;
  }

  .right-0 {
    right: 0;
  }

  .bottom-0 {
    bottom: 0;
  }

  .left-0 {
    left: 0;
  }

  .top-1 {
    top: 1rem;
  }

  .right-1 {
    right: 1rem;
  }

  .bottom-1 {
    bottom: 1rem;
  }

  .left-1 {
    left: 1rem;
  }

  .top-2 {
    top: 2rem;
  }

  .right-2 {
    right: 2rem;
  }

  .bottom-2 {
    bottom: 2rem;
  }

  .left-2 {
    left: 2rem;
  }
}
.top-auto {
  top: auto;
}

.right-auto {
  right: auto;
}

.bottom-auto {
  bottom: auto;
}

.left-auto {
  left: auto;
}

.transition {
  transition: all 0.2s ease;
}

.list-none {
  list-style-type: none;
}

.embed {
  @apply relative block h-0 p-0 overflow-hidden;
}

@responsive {
  .order-first {
    order: -9999;
  }

  .order-last {
    order: 9999;
  }

  .order-normal {
    order: 0;
  }

  .order-1 {
    order: 1;
  }

  .order-2 {
    order: 2;
  }

  .order-3 {
    order: 3;
  }

  .order-4 {
    order: 4;
  }

  .order-5 {
    order: 5;
  }

  .order-6 {
    order: 6;
  }

  .embed-21by9 {
    padding-bottom: 42.85714286%;
  }

  .embed-16by9 {
    padding-bottom: 56.25%;
  }

  .embed-3by2 {
    padding-bottom: 66.66666667%;
  }

  .embed-4by3 {
    padding-bottom: 75%;
  }

  .embed-3by4 {
    padding-bottom: 125%;
  }

  .embed-7by10 {
    padding-bottom: 143%;
  }

  .embed-1by1 {
    padding-bottom: 100%;
  }

  @screen md {
    .md-embed-1by1 {
      padding-bottom: 100%;
    }
  }
  .object-cover-absolute {
    @apply absolute inset-0 w-full h-full;
    object-fit: cover;
  }

  .object-contain {
    @apply absolute inset-0 w-full h-full;
    object-fit: contain;
  }

  .scroll-x-proximity {
    scroll-snap-type: proximity;
    scroll-snap-type: x proximity;
  }

  .scroll-align-start {
    scroll-snap-align: start;
  }
}
.bg-half-grey-lightest {
  @apply relative;
}
.bg-half-grey-lightest::before {
  @apply absolute top-0 left-0 right-0 h-1/2 bg-grey-lightest;
  content: "";
}

.slick-slide {
  @apply inline-block;
}

.h-24 {
  height: 24rem;
}

.-z-1 {
  z-index: -1;
}

.text-custom-blue {
  color: #1176ff;
}

.text-custom-lighter-blue {
  color: #58baee;
}

.select-wrapper {
  box-sizing: content-box;
  display: inline-block;
  border: 1px solid #909090;
  cursor: pointer;
}

.select-wrapper, .select-wrapper select {
  width: 70px;
  height: 26px;
  line-height: 26px;
}

.select-wrapper:hover {
  border-color: #9a9a9a;
}

.select-wrapper .holder {
  display: block;
  margin: 0 35px 0 5px;
  white-space: nowrap;
  overflow: hidden;
  cursor: pointer;
  position: relative;
  z-index: -1;
}

.select-wrapper select {
  margin: 0;
  position: absolute;
  z-index: 2;
  cursor: pointer;
  outline: none;
  opacity: 1;
}

.box {
  position: relative;
  border: 1px solid #0076ff;
  background-color: #fbfbfb;
  padding: 0.75rem;
}

.blue-check {
  position: absolute;
  right: 10px;
  top: 35%;
}

.checkout-step .pane {
  @apply hidden;
}
.checkout-step.active .pane {
  @apply block;
}

.fixed-sidebar {
  padding: 0.5rem 0;
  position: fixed;
  bottom: 50%;
  right: -5px;
  width: 80px;
  transition: right 0.2s ease;
}
.fixed-sidebar path {
  transition: transform 0.2s ease;
}
.fixed-sidebar.hide {
  right: -74px;
}
.fixed-sidebar.hide path {
  transform: rotateY(180deg) translateX(-80%);
}

.fixed-sidebar * {
  color: #fff;
}

.fixed-sidebar .chevron {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -15px;
  height: 30px;
  width: 20px;
  font-size: 12px;
  padding: 5px;
  cursor: pointer;
}

/* purgecss start ignore */
.lozad:not(.noblur) {
  filter: blur(8px);
}
.lozad:not(.noblur)[data-loaded=true] {
  filter: none;
}

/* purgecss end ignore */
.btn {
  @apply text-sm bg-primary font-sans font-normal uppercase tracking-widest rounded-sm px-1-75 py-1-25 text-white leading-none;
  transition: 0.2s ease;
}

.comment-list {
  @apply list-none pl-0;
}

.comment-list ol {
  list-style: none;
}

.comment-form p {
  @apply mb-4;
}

.comment-form input[type=text],
.comment-form input[type=email],
.comment-form input[type=url],
.comment-form textarea {
  @apply block appearance-none w-full py-1 px-2 mb-1 text-base leading-normal bg-white text-grey-darker border border-grey rounded;
}

.comment-form input[type=submit] {
  @apply text-sm bg-primary font-sans font-normal uppercase tracking-wide rounded-sm px-1-75 py-1-25 text-white leading-none bg-grey-dark;
}
.comment-form input[type=submit]:hover {
  @apply bg-grey-darker;
}

