.icaal-contact-form .submit:hover {
  cusor: pointer;
}

.alert {
  @apply rounded my-1 p-1 border bg-blue-lightest text-blue border-blue-lighter;
}
.alert.alert-success {
  @apply bg-green-lightest text-green border-green-lighter;
}
.alert.alert-danger {
  @apply bg-red-lightest text-red border-red-lighter;
}

.form-pane {
  display: none;
}
.form-pane.active {
  display: block;
}

.form-pane-option.active {
  border-color: #38c172;
}

.progress-bar-wrap {
  height: 6px;
  background-color: #ededed;
}
.progress-bar-wrap .progress-bar {
  background-color: #38c172;
  position: absolute;
  top: 0;
  left: 0;
  height: 6px;
  max-width: 100%;
  transition: all 0.25s ease-in-out;
}

/**
 * WordPress Generated Classes
 * @see http://codex.wordpress.org/CSS#WordPress_Generated_Classes
 */
/** Media alignment */
.alignnone {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
  height: auto;
}

.aligncenter {
  @apply my-2 mx-auto;
  display: block;
  height: auto;
}

.alignleft,
.alignright {
  @apply mb-4;
  height: auto;
}

@screen sm {
  .alignleft {
    @apply mr-4;
    float: left;
  }

  .alignright {
    @apply ml-4;
    float: right;
  }
}
/** Captions */
.wp-caption {
  @apply inline-block mb-4;
}

.wp-caption img {
  @apply mb-2 leading-none max-w-full h-auto;
}

.wp-caption-text {
  font-size: 90%;
  color: #6c757d;
}

/** Text meant only for screen readers */
.sr-only,
.screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.sr-only-focusable:active,
.screen-reader-text:active,
.sr-only-focusable:focus,
.screen-reader-text:focus {
  position: static;
  width: auto;
  height: auto;
  overflow: visible;
  clip: auto;
  white-space: normal;
}

.btn-toolbar a {
  @apply inline-block text-xs bg-primary font-sans font-normal uppercase tracking-wide rounded-sm leading-none;
  @apply text-white px-1 py-0-75 !important;
}
.btn-toolbar a:hover {
  @apply bg-blue-dark;
}

#header {
  @apply z-20;
}
@media (max-width: 991px) {
  #header .brand {
    @apply h-2;
  }
  #header .brand > img {
    @apply h-2 w-auto;
  }
}

@media (max-width: 992px) {
  .nav-primary {
    @apply invisible absolute right-0 w-full transition z-10;
    top: 51px;
    height: calc(100vh - 51px);
    transform: translateX(100%);
  }
  .nav-primary > ul {
    @apply w-5/6 ml-auto h-full bg-white border-t border-grey-light;
  }
  .nav-primary ul {
    @apply list-none pl-0;
  }
  .nav-primary ul li a {
    @apply block text-grey-darker font-normal border-b border-grey-light px-2 py-1;
  }
  .nav-primary ul li a:hover {
    @apply text-primary;
  }
  .nav-primary ul li.menu-item-has-children {
    @apply relative;
  }
  .nav-primary ul li.menu-item-has-children > a:hover {
    @apply text-grey-darker;
  }
  .nav-primary ul li.menu-item-has-children .toggle > svg {
    @apply text-grey-darker transition;
    transform: rotate(0);
  }
  .nav-primary ul li.menu-item-has-children .toggle > svg path {
    fill: currentColor;
  }
  .nav-primary ul li.menu-item-has-children .sub-menu {
    @apply bg-grey-lightest overflow-hidden hidden;
  }
  .nav-primary ul li.menu-item-has-children.open > a {
    @apply text-black border-primary;
  }
  .nav-primary ul li.menu-item-has-children.open > .toggle > svg {
    transform: rotate(180deg);
  }
  .nav-primary.open {
    @apply visible;
    transform: translateX(0);
  }

  .nav-backdrop {
    background-color: rgba(0, 0, 0, 0.5);
    top: 51px;
    height: calc(100vh - 51px);
  }
  .nav-backdrop.show {
    @apply visible opacity-100;
  }
}
@screen lg {
  .nav-primary > ul > li > a {
    @apply text-xs text-grey-darker font-normal uppercase tracking-wide inline-block px-1 py-2;
  }
  .nav-primary > ul > li > a:hover {
    @apply text-blue;
  }
  .nav-primary > ul > li > .sub-menu {
    @apply bg-grey-lightest;
    top: 54px;
  }
  .nav-primary > ul > li > .sub-menu > li > .sub-menu {
    @apply bg-white;
  }
  .nav-primary > ul > li:hover > .sub-menu {
    top: 70px;
  }

  .nav-toolbar > ul > li:not(.btn-toolbar) a {
    @apply px-1 py-0-5  font-normal;
  }
  .nav-toolbar > ul > li > .sub-menu {
    @apply bg-white z-50;
    top: 12px;
  }
  .nav-toolbar > ul > li:hover > .sub-menu {
    top: 28px;
  }

  .nav-has-dropdowns > ul {
    @apply list-none pl-0 flex flex-wrap items-center;
  }
  .nav-has-dropdowns > ul > li {
    @apply relative;
  }
  .nav-has-dropdowns > ul > li > .sub-menu {
    @apply absolute left-0 w-12 pl-0 list-none rounded-sm shadow border-b border-primary transition;
    visibility: hidden;
    opacity: 0;
  }
  .nav-has-dropdowns > ul > li:hover > .sub-menu {
    visibility: visible;
    opacity: 1;
  }
  .nav-has-dropdowns > ul > li > .sub-menu > li {
    @apply relative;
  }
  .nav-has-dropdowns > ul > li > .sub-menu > li a {
    @apply block text-xs text-grey-darker font-normal px-1 py-0-75;
  }
  .nav-has-dropdowns > ul > li > .sub-menu > li a:hover {
    @apply text-primary;
  }
  .nav-has-dropdowns > ul > li > .sub-menu > li > .sub-menu {
    @apply absolute top-0 w-12 pl-0 list-none rounded-sm shadow border-b border-primary;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.2s ease;
    transform: translateX(0);
  }
  .nav-has-dropdowns > ul > li > .sub-menu > li:hover > .sub-menu {
    visibility: visible;
    opacity: 1;
  }
}
footer .widget p,
footer .widget ul,
footer .widget li,
footer .widget a {
  @apply text-white;
}
footer .widget h3 {
  @apply text-xl font-serif text-white uppercase tracking-widest border-b border-white pb-2 mb-2;
}
footer .widget .menu {
  @apply list-none pl-0;
}
footer .widget .menu li {
  @apply mb-1;
}

